export const timeFormat = str => {
    if (str ){
        return  str.replace("@", " ").replace(/\./g, ":")
    }else {
        return ''
    }
}

export function dateFormat(format="yyyy-MM-dd hh:mm", date=undefined) {
    //       
    let _this
    if(!date){
        // 
        _this = new Date();
    }else {
        _this = date
    }
    let args = {
        //     +1
        "M+": _this.getMonth() + 1,
        "d+": _this.getDate(),
        "h+": _this.getHours(),
        "m+": _this.getMinutes(),
        "s+": _this.getSeconds(),
        "q+": Math.floor((_this.getMonth() + 3) / 3),  //quarter
        "S": _this.getMilliseconds()
    };
    if (/(y+)/.test(format))
        // /xxx/   
        // .test()    true  false
        format = format.replace(RegExp.$1, (_this.getFullYear() + "").substr(4 - RegExp.$1.length));
    // RegExp.$1 ==  == /(y+)/
    for (let i in args) {
        let n = args[i];
        if (new RegExp("(" + i + ")").test(format))
            format = format.replace(RegExp.$1, RegExp.$1.length === 1 ? n : ("00" + n).substr(("" + n).length));
    }
    return format
}

dateFormat("yyyy-MM-dd hh:mm:ss:S")